import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    orderprogressstatus:false,
    isclear:false,
    passengerinfo:[],
    tripinfo:{
      havesaved:-1,
      fromplace:{
        name:"",
        code:""
      },
      toplace:{
        name:"",
        code:""
      },
      traveldate:"",
    },
    count12306:{
      name:'',
      password:'',
      ispassed:-1
    }
  },

  mutations:{
    hiddenorderprogressbar(state,bbb){
      state.orderprogressstatus=false;
    },
    showorderprogressbar(state,bbb){
      state.orderprogressstatus=true;
    },
    savecount12306(state,countinfo){
      state.count12306=countinfo;
    },
    savetripinfo(state,tripinfo){
      state.tripinfo=tripinfo;
    },
    enableStatus(state){
      state.isclear=true
    },
    addpassenger(state,o){
      state.passengerinfo.push(o);
    },
    removepassenger(state,index){
      state.passengerinfo.splice(index,1);
    },
    editpassangerInfo(state,index,info){
      // state.passengerinfo
      state.passengerinfo[index]=info;
      // Vue.set(state.passengerinfo, index, info);
    }
  }
})



