<template>
  <div class="payinfo">
    <mt-heard title="支付"></mt-heard>
    <div class="totlalprice">
      <p class="price">应付总金额:￥{{this.price}}</p>

    </div>

    <div class="passengerInfo" v-for="(item,index) in passengerInfo">
      <div class="left">
        <p>{{item.passengerName}}
          <span>{{item.passengerType=='1'?'成人票':'儿童票'}}</span>
        </p>
        <p class="tip" v-if="item.passengerType=='1'">{{getIdCardType(item.passengerCardType)}}:{{item.passengerCardNumber}}</p>
      </div>
      <div class="right">
        <p>{{orderBookingPassengerVos[index].seatName+'￥'+orderinfo.seatPrice}}</p>
        <p class="tip">{{orderBookingPassengerVos[index].seatInfos}}</p>
      </div>
    </div>
    <div class="insurance" v-if="havinsurance">
      其他金额:
      <div class="insurance">
        <div class="insurance-item">
          <p>{{orderinfo.orderBookingPassengerVos[0].insuranceVos[0].insuranceName}}</p>
          <p>{{insurancePrice}}</p>
        </div>
      </div>
      <div class="rushiprice" v-if="orderinfo.buyTicketsType=='4'||orderinfo.buyTicketsType=='5'">
        <span>急速抢票套餐</span>
        <span>￥15</span>

      </div>
    </div>
    <div class="pay">
      <mt-button type="primary" size="large" @click="clickPay">支付</mt-button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import {
  Header,
  Field,
  Button,
  Popup,
  Indicator,
  Checklist,
  Toast,
  Switch
} from "mint-ui";
export default {
  name: "payinfo",
  data() {
    return {
      paytitle: "",
      orderinfo: null,
      openid: "",
      mainorderno: "",
      paytype: "",
      price: 0,
      planeinfo: [],
      peopleinfo: [],
      out_trade_no: "",
      sign: "",
      guestIp: "",
      where: "11",
      userphone: "",
      secret: "",
      userid: ""
    };
  },
  components: {
    "mt-button": Button,
    "mt-heard": Header
  },
  computed: {
    havinsurance() {
      if (this.orderinfo != null) {
        if (this.orderinfo.InsuranceVo != null) {
          if (this.orderinfo.InsuranceVo.length > 0) {
            return true;
          } else {
            return false;
          }
        } else {
          return false;
        }
      } else {
        return false;
      }
    },
    passengerInfo() {
      if (this.orderinfo != null) {
        return this.orderinfo.orderBookingPassengerInfos;
      }
    },
    orderBookingPassengerVos() {
      if (this.orderinfo != null) {
        return this.orderinfo.orderDetailPassengerVos;
      }
    },
    insuranceName() {
      if (this.orderinfo.orderBookingPassengerVos[0].insuranceVos.length > 0) {
        // console.log("保险",this.orderinfo.orderBookingPassengerVos[0].insuranceVos[0].insuranceName);
        return this.orderinfo.orderBookingPassengerVos[0].insuranceVos[0]
          .insuranceName;
      } else {
        return "";
      }
    },
    insurancePrice() {
      if (this.orderinfo.orderBookingPassengerVos[0].insuranceVos.length > 0) {
        // console.log("保险",this.orderinfo.orderBookingPassengerVos[0].insuranceVos[0]);
        return this.orderinfo.orderBookingPassengerVos[0].insuranceVos[0]
          .eachInsurancePrice;
      } else {
        return "";
      }
    }
  },
  methods: {
    getIdCardType(tt) {
      switch (tt) {
        case "1":
          return "身份证";
          break;
        case "2":
          return "儿童无证件";
          break;
        case "3":
          return "港澳通行证";
          break;
        case "4":
          return "台湾通行证";
          break;
        case "5":
          return "护照";
          break;
      }
    },

    clickPay() {
      console.log(this.price * 100);
      console.log(this.sign);
      console.log(this.out_trade_no);
      let gd_reutrnurl =
        "http://m.czgdly.com/transportation/pay/html/trainpaysuccess.html";
      let wd_returnurl =
        "http://m.wandaly.net/transportation/pay/html/trainpaysuccess.html";
      let returnurl = "";
      // if (location.hostname == "m.wandaly.net") {
      //   returnurl = wd_returnurl;
      // } else {
      //   returnurl = gd_reutrnurl;
      // }
      returnurl=`http://${window.location.host}/transportation/pay/html/trainpaysuccess.html`
      BC.click(
        {
          title: this.paytitle,
          amount: parseInt(this.price * 100), //2,
          out_trade_no: this.out_trade_no,
          sign: this.sign,
          return_url: returnurl,
          need_ali_guide: true,
          optional: {
            customerId: this.$cookie.get("USERIDGDLY"),
            customerSource: this.where,
            orderNo: this.mainorderno, //驴妈妈的订单号
            changeOrderId: 0,
            refundId: 0,
            ticketSerialNo: ""
          },
          analysis: {
            ip: this.guestIp
          },
          openid: this.openid
        },
        {
          wxJsapiFinish: function(res) {
            //jsapi接口调用完成后
            // alert(JSON.stringify(res));
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              // window.href=""
              window.location.href = returnurl;
            }
          }
        }
      );
    }
  },

  beforeRouteEnter: (to, from, next) => {
    // console.log("?????????===?", to.query.orderinfo)
    // var orderinfo = JSON.parse(to.query.orderinfo);
    (async () => {
      const getCookie = function getCookie(name) {
        var arr,
          reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
        if ((arr = document.cookie.match(reg))) return unescape(arr[2]);
        else return null;
      };
      const getOrderInfo = function getOrderInfo(
        orderNo,
        userId,
        usersercert,
        userPhone,
        where
      ) {
        return axios.get(
          "http://58.216.175.118:86/api/LvmamaTrainTickets/TrainLocalOrders/GetTrainOrderByNo/" +
            orderNo +
            "?token=" +
            userId +
            "|" +
            usersercert +
            "|" +
            userPhone +
            "|" +
            where
        );
      };
      const _orderno = to.query.partnerOrderNo;
      const _userId = getCookie("USERIDGDLY");
      const _usersercert = getCookie("usersecret");
      const _userPhone = getCookie("gdmobileuserphone");
      const _where = getCookie("WHERE");
      const res_orderinfo = await getOrderInfo(
        _orderno,
        _userId,
        _usersercert,
        _userPhone,
        _where
      );
      const orderinfo = res_orderinfo.data;
      console.log("res_orderinfo", orderinfo);
      let gd_paysignurl =
        "http://m.czgdly.com/transportation/airplane/Beecloud/getsign.asp"; //光大的
      let wd_paysignurl =
        "http://m.wandaly.net/transportation/gdtrain/Beecloud/getsign.asp"; //万达的
      let paysignurl = "";
      if (location.hostname == "m.wandaly.net") {
        paysignurl = wd_paysignurl;
      } else {
        paysignurl = gd_paysignurl;
      }
      let amount = orderinfo.needPayPrice * 100;
      let title =
        orderinfo.contactName +
        ",预定" +
        orderinfo.orderDetailPassengerVos.length +
        "张火车票";
      let orderno = orderinfo.partnerOrderNo;
      let getIp = function() {
        return axios.get(
          "http://m.czgdly.com/transportation/gdtrain/beecloud/getCurrentIp.asp"
        );
      };
      let getSign = function(t_title, t_orderno, t_price) {
        return axios.get(paysignurl, {
          params: {
            title: t_title,
            amount: parseInt(amount), // amount,
            orderno: t_orderno
          }
        });
      };

      axios.all([getIp(), getSign(title, orderno, amount)]).then(
        axios.spread(function(ip, sig) {
          // console.log("res_orderinfo", res_orderinfo);
          console.log("ip", ip.data);
          console.log("sign", sig);
          Indicator.close();
          next(vm => {
            if (vm.$cookie.get("WHERE")) {
              vm.where = vm.$cookie.get("WHERE");
            }
            vm.paytitle = title;
            vm.mainorderno = orderinfo.orderNo;
            // vm.planeinfo = to.params.plane;
            // vm.peopleinfo = to.params.passenger;
            vm.price = orderinfo.needPayPrice;
            vm.out_trade_no = orderinfo.partnerOrderNo;
            vm.sign = sig.data;
            vm.guestIp = ip.data;
            vm.orderinfo = orderinfo;
            if (
              vm.$cookie.get("WXOPID") != "" ||
              vm.$cookie.get("WXOPID") != null
            ) {
              vm.openid = vm.$cookie.get("WXOPID");
            }
            console.log("订单数据", vm.orderinfo);
          });
        })
      );
    })();
  }
};
</script>

<style scoped>
.price {
  background-color: #fff;
  text-align: left;
  font-size: 20px;
}
.pay {
  padding: 0 10px;
  background-color: #fff;
}
.totlalprice {
  background-color: #fff;
  padding: 10px 0;
}
.payinfo {
  height: 100%;
  background-color: #fff;
}
.passengerInfo {
  padding: 10px;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.passengerInfo .left {
  text-align: left;
  color: #666;
}
.passengerInfo .left span {
  background-color: #26a2ff;
  color: #fff;
  font-size: 12px;
  padding: 5px;
  border-radius: 5px;
}
.passengerInfo .left .tip {
  color: #666;
  font-size: 12px;
  padding-top: 5px;
}
.passengerInfo .left {
  font-size: 17px;
}
.passengerInfo .right {
  font-size: 17px;
}
.passengerInfo .right .tip {
  font-size: 12px;
}
.insurance-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 7px 10px;
}
</style>



