<template>
  <div class="loginpage">
    <mt-header title="登陆"></mt-header>
    <div class="logingcontent">
        <div class="user">
          <mt-field placeholder="请输入登陆手机号" :state="loginphonestate" v-model="contactMobile">
          </mt-field>
        </div>
        <div class="user">
          <mt-field placeholder="请输入验证码" v-model="logincode">
            <mt-button @click="clicklogincode" :disabled="issended" size="small" type="primary">{{tttime==-1?'发送验证码':tttime}}</mt-button>
          </mt-field>
        </div>
        <div class="subbutton">
          <mt-button size="large" @click="clicklogin" :disabled="loginbuttonenabel" type="primary">{{loginbuttonstatus==''?'登陆':loginbuttonstatus}}</mt-button>

        </div>
      </div>
  </div>
</template>

<script>
import { Header, Field, Button, Indicator, Toast } from "mint-ui";
import axios from "axios";

export default {
  name: "login",
  components: {
    "mt-field": Field,
    "mt-button": Button,
    "mt-header": Header
  },
  data() {
    return {
      logincode: "",
      contactMobile: "",
      issended: false,
      tttime: -1,
      loginbuttonenabel: false,
      loginbuttonstatus: "",
      fullPath:"/"
    };
  },
  computed:{
    loginphonestate(){
      if (this.contactMobile == "") {
        return "";
      } else {
        if (this.checkPhone(this.contactMobile)) {
          this.loginbuttonenabel = false;
          this.issended = false;

          return "success";
        } else {
          this.loginbuttonenabel = true;
          this.issended = true;
          return "error";
        }
        // this.loginphonestate="error"
      }
    }
  },
  methods: {
     checkPhone(v) {
      // var phone = document.getElementById('phone').value;
      if (!/^1[34578]\d{9}$/.test(v)) {
        return false;
      } else {
        return true;
      }
    },
    showTime(count) {
      this.tttime = count + "秒后重新发送";
      this.issended = true;

      console.log(this.tttime);
      if (count == 0) {
        // do job
        this.tttime = -1;
        this.issended = false;
      } else {
        count -= 1;
        setTimeout(() => {
          this.showTime(count);
        }, 1000);
      }
    },
    clicklogin() {
      this.loginbuttonenabel = true;
      this.loginbuttonstatus = "登陆中...";
      // if(this.logincode=="123"){
      //   this.loginbuttonstatus = "登陆成功";
      //   this.$cookie.set("gdmobileusername",'15861147205');
      //   setTimeout(() => {
      //         this.loginpopup = false;
      //       }, 1000);
      // }else{
      //   this.loginbuttonstatus = "验证码不正确";
      //       setTimeout(() => {
      //         this.loginbuttonstatus = "登陆";
      //         this.loginbuttonenabel = false;
      //       }, 2000);
      // }
      axios
        .get(
          "http://m.czgdly.com/transportation/gdtrain/Beecloud/login/sendcode.asp",
          {
            params: {
              c: "2",
              tel: this.contactMobile,
              name: this.contactName,
              val: this.logincode
            }
          }
        )
        .then(res => {
          console.log("登陆返回的信息", res.data.message);
          if (res.data.message == "success") {
            this.loginbuttonstatus = "登陆成功";
            //this.$store.commit("setcercet", res.data.data.usersecret); //存入state中
            // this.$store.commit("setuserId", res.data.data.userid);
            // this.$store.commit("setUserPhone", this.contactMobile);
            this.$cookie.set("gdmobileusername", this.contactMobile, 7);

            this.$cookie.set("USERIDGDLY", res.data.data.userid, 7);
            this.$cookie.set("gdmobileuserphone", this.contactMobile, 7);
            this.$cookie.set("usersecret", res.data.data.usersecret, 7);

            setTimeout(() => {
              //这里要跳转
              this.$router.push(this.fullPath);
            }, 1000);
          } else {
            this.loginbuttonstatus = "验证码不正确";
            setTimeout(() => {
              this.loginbuttonstatus = "登陆";
              this.loginbuttonenabel = false;
            }, 2000);
          }
        })
        .catch(error => {
          console.log("出错啦");
          this.loginbuttonenabel = false;
          this.loginbuttonstatus = "登陆";
        });
    },
    clicklogincode() {
      //发送验证码
      this.showTime(10);
      axios
        .get(
          "http://m.czgdly.com/transportation/gdtrain/Beecloud/login/sendcode.asp",
          {
            params: { tel: this.contactMobile, c: "1" }
          }
        )
        .then(res => {
          console.log("发送验证码返回的信息", res);
        })
        .catch(error => {});
    }
  },
  beforeRouteEnter: (to, from, next) => {
    // ...
    // console.log("from",from);
    // console.log("to",to.fullPath);
    next(vm=>{
      vm.fullPath=to.fullPath;
    });
  }
};
</script>

<style>
.loginpage {
  height: 100%;
  background-color: #fff;
}
.logingcontent {
  position: relative;
  top: 30%;
  transform: translateY(-30%);
  padding: 10px;
}
</style>



