// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";
import router from "./router";
import store from "./store/store";
import MintUI from "mint-ui";
import "mint-ui/lib/style.css";
import VueCookie from "vue-cookie";
import { InfiniteScroll } from "mint-ui";
import { getopenid } from "./assets/js/wechat";
getopenid();
Vue.use(VueCookie);
Vue.use(InfiniteScroll);

// import _ from 'lodash';
// Vue.prototype._ = _
// import Vuex from "vuex";
// Vue.use(Vuex);
Vue.config.productionTip = false;
/* eslint-disable no-new */
// const store = new Vuex.Store({
//   state: {
//     isclear: true
//   },
//   mutations: {
//     enableStatus(state) {
//       state.isclear = true;
//     }
//   }
// });
router.beforeEach((to, from, next) => {
  console.log("全局守卫", to);
  //判断是否登陆在特定的pages;
  //  if(to.path=="/f"||)
  switch (to.path) {
    case "/f":
    case "/orderdetails":
    case "/pay":
    case "/payGradorder":
    case "/list":
    case "/reutrnSuccsee":
      console.log("需要登陆哦");

      let phone = VueCookie.get("gdmobileuserphone");
      let name = VueCookie.get("gdmobileusername");
      let serc = VueCookie.get("usersecret");
      if(phone==null ||phone=="" ||name==""||name==null||serc==""||serc==null){
        router.push("/login");
        MintUI.Toast("请先登录");
      }else{
        next();
      }
      break;
    default:
      console.log("不需要登陆");
      next();
      break;
  }
});
new Vue({
  el: "#app",
  router,
  store,
  template: "<App/>",
  components: { App }
});



